// closing the pop-up notification in header section
$('.pop-up-close').on('click', function () {
  // closing the pop-up dialog
  $('.pop-up').addClass('hide');
//  readjusting the navbar to top
  $('.navbar').addClass('slide-up');
  $('.navbar-collapse').addClass('slide-up');
});
// navbar toggler click event
$('.navbar-toggler').on('click', function () {
  $(this).toggleClass('show');
  $('.top-bar').toggleClass('show');
  $('.middle-bar').toggleClass('show');
  $('.bottom-bar').toggleClass('show');
});

// Start -> stop playing video in iframe when modal is closed
$("#exampleModalCenter").on('hidden.bs.modal', function (e) {
  $("#exampleModalCenter iframe").attr("src", $("#exampleModalCenter iframe").attr("src"));
});

// End -> stop playing video in iframe when modal is closed

// changing navbar height on scroll
$(window).on('scroll', function () {
  $(".navbar").toggleClass("zoom-in", $(window).scrollTop() > 100);
});

