import Cookies from 'js-cookie';

const cookieName = '_job_ad_shown';

const cookieOptions = {
  sameSite: 'lax',
};

const hasCookie = Cookies.get(cookieName, cookieOptions);

if (!hasCookie) {
  $(window).ready(setTimeout(() => {
    $('#popup-banner-modal').modal('show');
    $('.popup-banner-modal').toggleClass('in');
    $('#modal-close').modal('hide');
    Cookies.set(cookieName, 1, cookieOptions)
  }, 6000));
}
