import 'parsleyjs'

const $form = $("#apply-for-job");

$form.parsley();

$form.off('submit').on('submit.rb', function (e) {
  e.preventDefault();
  e.stopPropagation();
  console.log('onSubmitClick');

  // Workaround to prevent double event
  // @source https://github.com/guillaumepotier/Parsley.js/issues/1184
  if (typeof e.originalEvent === 'undefined' || e.isTrigger) {
    console.log('Prevent duplicate events');
    return false;
  }
  // Validate form fields
  $(this).parsley().validate();

  // If the form is valid, will proceed with submission.
  if ($(this).parsley().isValid()) {
    console.log("Release the form for submission")
    $(this).find(':submit').prop('disabled', true);
    $(this).get(0).submit();
  }

})

var $attachResume = $('#attachResume');
$('#custom-button').on('click', function () {
  $attachResume.trigger('click');
});

$attachResume.on('change', function () {
  var $fileName = $('#custom-text');

  if ($(this).val()) {
    $fileName.text($(this).get(0).value.match(
      /[\/\\]([\w\d\s\.\-\(\)]+)$/
    )[1]);
  } else {
    $fileName.text("No file chosen");
  }
});

// Custom validation
window.Parsley.addValidator('maxFileSize', {
  validateString: function (_value, maxSize, parsleyInstance) {
    let files = parsleyInstance.$element[0].files;
    return files.length != 1 || files[0].size <= maxSize * 1024;
  },
  requirementType: 'integer',
  messages: {
    en: 'This file should not be larger than %s Kb',
  }
});
